body {
  --blue: #00acdc;
  --after-height: 4px;
  margin: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-bottom: 10px;
  padding-top: 10px;
}

.nav {
  height: 60px;
  margin-left: 0;
  display: flex;
}

.nav_list {
  align-items: center;
  padding-left: 0;
  display: flex;
}

.nav_item {
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  list-style: none;
  display: inline-block;
  position: relative;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  padding-top: 23px;
  padding-bottom: 23px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav-link:hover {
  color: #00acdc;
  position: relative;
}

.is-active {
  color: #00acdc;
  margin-left: 0;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
